@import "./../../scss/variables.scss";
@import "./../../scss/mixins.scss";

.review-item {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  width: 550px;
  padding: 16px 20px;
  align-items: flex-start;
  gap: 34px;
  border-radius: 40px;
  border: 2px solid $black;
  background: $white;
  color: $black;

  &__author {
    @include mp-null;
    padding-bottom: 12px;
    font-family: $font-family;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 1.5;
    text-transform: uppercase;
  }

  &__date {
    @include mp-null;
    padding-bottom: 8px;
    font-family: $font-family;
    font-size: 14px;
    font-style: normal;
    font-weight: 300;
    line-height: 1.57;
  }

  &__text {
    @include mp-null;
    font-family: $font-family;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.22;
    overflow-y: auto;
    max-height: 200px;

    &::-webkit-scrollbar {
      width: 4px;
      background-color: transparent;
      border-radius: 20px;
      cursor: pointer;
    }

    &::-webkit-scrollbar-thumb {
      width: 4px;
      background-color: $gray;
      border-radius: 20px;
    }
  }

  &__rating-container {
    display: flex;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    gap: 8px;
  }

  &__rating {
    @include mp-null;
    font-family: $font-family;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.5;
  }
}
